import { createSharedShopify, ShopifyClientConfig } from "../shared/createClient";
import { getProduct } from "@core/getProduct";
import { getCollection } from "@core/getCollection";
import { GetProductOptions } from "@t/product";
import { GetCollectionOptions } from "@t/collections";

export function createShopifyClient(config: ShopifyClientConfig) {
  const { fetchShopify } = createSharedShopify(config);

  return {
    fetchShopify,
    getProduct: (args: GetProductOptions) => getProduct(fetchShopify, args),
    getCollection: (args: GetCollectionOptions) => getCollection(fetchShopify, args),
  };
}
